@import "theme/print.scss";



// Template | Remove or Integrate

// uikit/base
a, .uk-link {
	color: $nbRed;
}

.uk-link-toggle:focus .uk-link,
.uk-link-toggle:hover .uk-link,
.uk-link:hover,
a:hover {
	color: $nbRed;
}

// uikit/utility
.uk-logo {

	img {
		width: 228px;
	}
}

// uikit/form
.uk-input:not(.uk-form-danger),
.uk-select:not(.uk-form-danger),
.uk-textarea:not(.uk-form-danger),
.uk-checkbox:not(.uk-form-danger),
.uk-radio:not(.uk-form-danger),
.uk-checkbox:checked:not(.uk-form-danger),
.uk-radio:checked:not(.uk-form-danger) {
	border-color: $nbBlack;
}

// theme/footer
.footer {
	font-size: 0.9rem;

	@media (max-width: $breakpointMobile) {
		font-size: 0.8rem;
	}
}

