html, body {
  overflow-x: hidden;
  font-family: "TT Norms", sans-serif;
  width: 100%;
  position: relative;
  font-size: 16px;
}

.bg-black {
  background-color: #000;
}

.color-black {
  color: #000;
}

.bg-white {
  background-color: #fff;
}

.color-white {
  color: #fff;
}

a,
button {
  appearance: none;
  outline: none;
  transition: 256ms ease-in-out;
}

a:hover, a:focus, a:active {
  text-decoration: none;
}

button,
input[type='button'],
input[type='submit'] {
  background: none;
  border: 0;
  cursor: pointer;
  outline: none;
  padding: 0;
}

.uk-button {
  font-weight: 700;
  font-size: 14px;
  font-feature-settings: 'liga'off;
  color: #fff;
  border-radius: 50px;
  text-align: left;
  line-height: 1.3em;
  border: 0;
  padding: 15px 35px;
  transition: 256ms ease-in-out;
  text-transform: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.uk-button * {
  transition: inherit;
}

.uk-button.uk-button-primary, .uk-button.uk-button-default, .uk-button.uk-button-secondary, .uk-button.uk-button-danger {
  border-radius: 50px;
}

.uk-button.uk-button-primary, .uk-button.uk-button-secondary {
  background-color: #267cb9;
}

.uk-button.uk-button-danger {
  background-color: #f2827f;
}

.uk-button > span:last-of-type {
  display: inline-flex;
  align-items: center;
  position: relative;
  margin-left: 15px;
}

.uk-button > span:last-of-type::after {
  position: absolute;
  left: 0;
  height: 1px;
  background-color: #fff;
  content: '';
  width: 0;
  top: 50%;
  transform: translateY(-50%);
  transition: .7s ease;
}

.uk-button > span:last-of-type svg {
  position: relative;
  left: 0;
  transition: .7s ease;
}

.uk-button.uk-button-small {
  padding: 9px 25px;
}

.uk-button.uk-button-small > span:last-of-type {
  margin-left: 10px;
}

.uk-button.uk-button-large {
  padding: 18px 40px;
  font-size: 16px;
}

.uk-button:hover, .uk-button:focus, .uk-button:active {
  background-color: #1870af;
}

.uk-button:hover > span:last-of-type::after, .uk-button:focus > span:last-of-type::after, .uk-button:active > span:last-of-type::after {
  width: 18px;
}

.uk-button:hover > span:last-of-type svg, .uk-button:focus > span:last-of-type svg, .uk-button:active > span:last-of-type svg {
  left: 18px;
}

.uk-button:hover.uk-button-danger, .uk-button:focus.uk-button-danger, .uk-button:active.uk-button-danger {
  background-color: #f2827f;
}

.uk-button-default {
  border: 2px solid #eff1f4;
  background-color: transparent;
  padding: 13px 33px;
  color: #1b365d;
}

.uk-button-default > span:last-of-type::after {
  background-color: #267cb9;
}

.uk-button-default > span:last-of-type .uk-svg:not(.uk-preserve) [fill*='#']:not(.uk-preserve) {
  fill: #267cb9;
}

.uk-button-default.uk-button-small {
  padding: 7px 23px;
}

.uk-button-default.uk-button-large {
  padding: 16px 38px;
}

.uk-button-default:hover, .uk-button-default:focus, .uk-button-default:active {
  background-color: transparent;
  border-color: #cbd2dc;
}

.uk-button-secondary {
  background-color: #1b365d;
}

.uk-button-secondary:hover, .uk-button-secondary:focus, .uk-button-secondary:active {
  background-color: #0c2549;
}

.uk-button-text {
  min-height: 0;
  padding: 10px 25px 10px 1px;
  transition: .5 linear;
  min-width: auto;
  overflow: visible;
  font-size: 14px;
  color: #1b365d;
  position: relative;
}

.uk-button-text::before {
